"use client"

import Link from 'next/link'
import { useLocalData } from '@/hooks/useLocalData'
import { formatCurrency, formatDate } from '@/lib/utils'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'

export default function AdminDashboard() {
  const { data, loading, error } = useLocalData()

  // Calculate admin statistics
  const totalOrders = data.orders.length
  const pendingOrders = data.orders.filter(order => order.status === 'pending').length
  const totalRevenue = data.orders.reduce((sum, order) => sum + order.total, 0)
  const totalCommission = data.orders.reduce((sum, order) => sum + order.commission, 0)
  const activeFarms = data.farms.length
  const activeRestaurants = data.restaurants.length

  // Recent activity
  const recentOrders = data.orders
    .sort((a, b) => new Date(b.orderDate).getTime() - new Date(a.orderDate).getTime())
    .slice(0, 5)

  // Low stock alerts across all farms
  const lowStockProducts = data.farms.flatMap(farm => 
    farm.products
      .filter(product => product.quantity < 10)
      .map(product => ({ ...product, farmName: farm.name }))
  )

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <div className="text-lg">Loading admin dashboard...</div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <div className="text-red-800">Error: {error}</div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
        <p className="text-gray-600">Manage the FarmFresh distribution network</p>
      </div>

      {/* Key Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Orders</CardTitle>
            <div className="w-4 h-4 bg-blue-500 rounded"></div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{totalOrders}</div>
            <p className="text-xs text-muted-foreground">
              {pendingOrders} pending
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Revenue</CardTitle>
            <div className="w-4 h-4 bg-green-500 rounded"></div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(totalRevenue)}</div>
            <p className="text-xs text-muted-foreground">
              All time revenue
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Commission Earned</CardTitle>
            <div className="w-4 h-4 bg-purple-500 rounded"></div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(totalCommission)}</div>
            <p className="text-xs text-muted-foreground">
              10% commission rate
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Network Size</CardTitle>
            <div className="w-4 h-4 bg-orange-500 rounded"></div>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{activeFarms + activeRestaurants}</div>
            <p className="text-xs text-muted-foreground">
              {activeFarms} farms, {activeRestaurants} restaurants
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Quick Actions */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Link href="/admin/order-routing">
          <Card className="hover:shadow-lg transition-shadow cursor-pointer">
            <CardHeader>
              <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center mb-4">
                <div className="w-6 h-6 bg-blue-500 rounded"></div>
              </div>
              <CardTitle>Order Routing</CardTitle>
              <CardDescription>
                Manage order logistics and routing
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="flex justify-between items-center">
                <span className="text-sm text-gray-500">Pending Orders</span>
                <Badge variant="destructive">{pendingOrders}</Badge>
              </div>
              <Button variant="outline" className="w-full mt-4">
                Manage Orders →
              </Button>
            </CardContent>
          </Card>
        </Link>

        <Link href="/admin/farmer-management">
          <Card className="hover:shadow-lg transition-shadow cursor-pointer">
            <CardHeader>
              <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center mb-4">
                <div className="w-6 h-6 bg-green-500 rounded"></div>
              </div>
              <CardTitle>Network Management</CardTitle>
              <CardDescription>
                Manage farms and restaurants
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <div className="flex justify-between items-center text-sm">
                  <span className="text-gray-500">Farms</span>
                  <span className="font-medium">{activeFarms}</span>
                </div>
                <div className="flex justify-between items-center text-sm">
                  <span className="text-gray-500">Restaurants</span>
                  <span className="font-medium">{activeRestaurants}</span>
                </div>
              </div>
              <Button variant="outline" className="w-full mt-4">
                Manage Network →
              </Button>
            </CardContent>
          </Card>
        </Link>

        <Link href="/admin/commission-tracking">
          <Card className="hover:shadow-lg transition-shadow cursor-pointer">
            <CardHeader>
              <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center mb-4">
                <div className="w-6 h-6 bg-purple-500 rounded"></div>
              </div>
              <CardTitle>Commission Tracking</CardTitle>
              <CardDescription>
                Track earnings and export reports
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="text-center">
                <div className="text-2xl font-bold text-purple-600">
                  {formatCurrency(totalCommission)}
                </div>
                <div className="text-sm text-gray-500">Total Earned</div>
              </div>
              <Button variant="outline" className="w-full mt-4">
                View Reports →
              </Button>
            </CardContent>
          </Card>
        </Link>

        <Link href="/admin/delivery-scheduling">
          <Card className="hover:shadow-lg transition-shadow cursor-pointer">
            <CardHeader>
              <div className="w-12 h-12 bg-orange-100 rounded-lg flex items-center justify-center mb-4">
                <div className="w-6 h-6 bg-orange-500 rounded"></div>
              </div>
              <CardTitle>Delivery Scheduling</CardTitle>
              <CardDescription>
                Schedule and manage deliveries
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="text-center">
                <div className="text-lg font-medium">
                  {data.orders.filter(o => o.status === 'confirmed').length}
                </div>
                <div className="text-sm text-gray-500">Ready for Delivery</div>
              </div>
              <Button variant="outline" className="w-full mt-4">
                Schedule Deliveries →
              </Button>
            </CardContent>
          </Card>
        </Link>
      </div>

      {/* Recent Activity */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Recent Orders</CardTitle>
            <CardDescription>
              Latest orders in the system
            </CardDescription>
          </CardHeader>
          <CardContent>
            {recentOrders.length > 0 ? (
              <div className="space-y-4">
                {recentOrders.map(order => (
                  <div key={order.id} className="flex justify-between items-center p-3 border rounded-lg">
                    <div>
                      <div className="font-medium">Order #{order.id.slice(-4)}</div>
                      <div className="text-sm text-gray-500">
                        {order.restaurantName} • {formatDate(order.orderDate)}
                      </div>
                    </div>
                    <div className="text-right">
                      <Badge className={
                        order.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                        order.status === 'confirmed' ? 'bg-blue-100 text-blue-800' :
                        order.status === 'delivered' ? 'bg-green-100 text-green-800' :
                        'bg-red-100 text-red-800'
                      }>
                        {order.status}
                      </Badge>
                      <div className="text-sm font-medium mt-1">
                        {formatCurrency(order.total)}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-8">
                <div className="text-gray-500">No recent orders</div>
                <p className="text-sm text-gray-400 mt-2">
                  Orders will appear here as they are placed
                </p>
              </div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>System Alerts</CardTitle>
            <CardDescription>
              Important notifications and alerts
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {pendingOrders > 0 && (
                <div className="flex items-center p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                  <div className="w-2 h-2 bg-yellow-500 rounded-full mr-3"></div>
                  <div className="flex-1">
                    <div className="font-medium text-yellow-800">
                      {pendingOrders} Pending Orders
                    </div>
                    <div className="text-sm text-yellow-600">
                      Orders waiting for confirmation
                    </div>
                  </div>
                </div>
              )}

              {lowStockProducts.length > 0 && (
                <div className="flex items-center p-3 bg-red-50 border border-red-200 rounded-lg">
                  <div className="w-2 h-2 bg-red-500 rounded-full mr-3"></div>
                  <div className="flex-1">
                    <div className="font-medium text-red-800">
                      {lowStockProducts.length} Low Stock Items
                    </div>
                    <div className="text-sm text-red-600">
                      Products running low across farms
                    </div>
                  </div>
                </div>
              )}

              {pendingOrders === 0 && lowStockProducts.length === 0 && (
                <div className="text-center py-8">
                  <div className="text-green-600 font-medium">All Systems Normal</div>
                  <p className="text-sm text-gray-500 mt-2">
                    No alerts at this time
                  </p>
                </div>
              )}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Low Stock Alert Details */}
      {lowStockProducts.length > 0 && (
        <Card className="border-red-200 bg-red-50">
          <CardHeader>
            <CardTitle className="text-red-800">Low Stock Alert</CardTitle>
            <CardDescription className="text-red-600">
              Products across the network that need restocking
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {lowStockProducts.slice(0, 6).map(product => (
                <div key={`${product.farmId}-${product.id}`} className="bg-white border border-red-200 rounded-lg p-3">
                  <div className="font-medium">{product.name}</div>
                  <div className="text-sm text-gray-600">{product.farmName}</div>
                  <div className="flex justify-between items-center mt-2">
                    <Badge variant="destructive">
                      {product.quantity} left
                    </Badge>
                    <span className="text-sm text-gray-500">{product.category}</span>
                  </div>
                </div>
              ))}
            </div>
            {lowStockProducts.length > 6 && (
              <div className="text-center mt-4">
                <span className="text-sm text-red-600">
                  And {lowStockProducts.length - 6} more items...
                </span>
              </div>
            )}
          </CardContent>
        </Card>
      )}

      {/* Performance Summary */}
      <Card>
        <CardHeader>
          <CardTitle>Network Performance</CardTitle>
          <CardDescription>
            Overview of the distribution network performance
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="text-center">
              <div className="text-3xl font-bold text-blue-600">
                {((data.orders.filter(o => o.status === 'delivered').length / totalOrders) * 100 || 0).toFixed(1)}%
              </div>
              <div className="text-sm text-gray-500">Order Fulfillment Rate</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-green-600">
                {(totalRevenue / totalOrders || 0).toFixed(0)}
              </div>
              <div className="text-sm text-gray-500">Average Order Value</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-purple-600">
                {data.farms.reduce((sum, farm) => sum + farm.products.length, 0)}
              </div>
              <div className="text-sm text-gray-500">Total Products Available</div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
